.logo {
  position: absolute;
  font-family: $font-2;
  font-size: 1.7rem;
  user-select: none;
  cursor: cell;
  transition: 1s;

  &:after {
    content: "";
    height: 38px;
    width: 38px;
    border: 1px $color-2 solid;
    border-radius: 50%;
    position: absolute;
    top: -5px;
    left: -15px;
    z-index: -1;
    transition: 2s;
  }

  &:hover:after {
    height: 88px;
    width: 88px;
    border: 2px $color-4 solid;
    left: -95px;
    top: 10px;
    transition: 2s;

    transform: rotateY(720deg);
  }
  &:hover {
    font-weight: 600;
    color: goldenrod;
    transition: 3s;
  }
}
