.cursor {
  width: 82px;
  height: 82px;
  border: 1px solid;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: 200ms ease-out;
  z-index: 2;
  backdrop-filter: brightness(150%);
  filter: hue-rotate(90deg);
  box-shadow: 13px 47.6px 21.2px -17px rgba(48, 197, 22, 0.349),
    21px 76.9px 27.3px -17px rgba(22, 197, 159, 0.349),
    27.4px 100.4px 29.9px -17px rgba(22, 95, 197, 0.349),
    33.5px 122.7px 31.7px -17px rgba(130, 22, 197, 0.349),
    40.3px 147.9px 35.1px -17px rgba(197, 22, 75, 0.349),
    51px 187px 51px -17px rgba(197, 107, 22, 0.349);

  &.hovered {
    width: 40px;
    height: 40px;
  }
}
