@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/Questrial-Regular.ttf);
}
@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/Quicksand-Light.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", serif;

$color-1: #332732;
$color-2: #ffd8d3;
$color-3: linear-gradient(
  to right,
  #ff8177 0%,
  #ff867a 0%,
  #ff8c7f 21%,
  #f99185 52%,
  #cf556c 78%,
  #bd547b 100%
);
$color-4: #ffe6e6;
$black: #fffffe;
$red: rgb(253, 87, 87);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;

  &::selection {
    color: $color-1;
    background: $color-2;
  }
}
html {
  scroll-behavior: unset;
}
a,
a:visited {
  color: $color-4;
}
body {
  font-family: $font-1;
  height: 100vh;
  background-color: $color-1;
  color: $black;
  overflow: hidden;

  @media screen and (max-width: 430px) {
    position: relative;
    max-width: 100vw;
    overflow-y: initial;
  }
}
main {
  position: relative;
}
.button {
  background: $color-3;
  height: 55px;
  width: 126px;
  border-radius: 50px;
  margin-top: 20px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: 0.2s;

  &:hover {
    letter-spacing: 1px;
    box-shadow: 2.8px 2.8px 2.2px rgba(255, 255, 255, 0.319),
      6.7px 6.7px 5.3px rgba(254, 254, 254, 0.2),
      12.5px 12.5px 10px rgba(255, 255, 255, 0.1),
      22.3px 22.3px 17.9px rgba(255, 255, 255, 0.042),
      41.8px 41.8px 33.4px rgba(255, 255, 255, 0.05),
      100px 100px 80px rgba(255, 255, 255, 0.07);
    color: $color-2;
  }
}
